import { shortcut } from '@engine/core';
import { setSingleSelectRadioFontSize } from './radio_group.service';
import { Label, Radio } from '@fui/core';

@shortcut()
export class ParameterRadioGroupSingleSelectRadioItem extends BI.BasicButton {
    public static xtype = 'report.main.parameter_container.controller.radio_group.sinlge_select_radio_item';

    public text: Label;

    public radio: Radio;
    
    public options = this.props;

    public props : {
        baseCls: string;
        $testId: string;
        hgap: number;
        text: string;
        value?: string;
        width?: number;
        adaptive: boolean;
        scale: number;
    } = {
        baseCls: 'report-main-parameter-container-controller-radio-group-radio-item',
        $testId: 'bi-single-select-radio-item',
        hgap: 4,
        text: '',
        value: undefined,
        width: undefined,
        adaptive: false,
        scale: 1,
    };
    
    public render() {
        const { scale, adaptive, hgap, text, value } = this.options;

        const defaultHeight = 24 * scale;

        const radioWidth = Math.floor(12 * scale);

        const items = [{
            el: {
                type: BI.VerticalAdaptLayout.xtype,
                items: [{
                    type: BI.Radio.xtype,
                    ref: (_ref: Radio) => {
                        this.radio = _ref;
                    },
                    width: radioWidth,
                    height: radioWidth,
                    iconWidth: radioWidth - 2,
                    iconHeight: radioWidth - 2,
                }],
            },
            width: radioWidth,
        }, {
            type: BI.Label.xtype,
            ref: (_ref: Label) => {
                this.text = _ref;
            },
            cls: 'list-item-text',
            textAlign: 'left',
            whiteSpace: adaptive ? 'normal' : 'nowrap',
            hgap: hgap * scale,
            text,
            value,
            textHeight: adaptive ? undefined : defaultHeight,
        }];

        const cls = setSingleSelectRadioFontSize('report-main-parameter-container-controller-radio-group-radio-item', radioWidth);

        return adaptive ? {
            type: BI.TableAdaptLayout.xtype,
            cls,
            horizontalAlign: BI.HorizontalAlign.Left,
            verticalAlign: BI.VerticalAlign.Middle,
            columnSize: [radioWidth, ''],
            items,
        } : {
            type: BI.HTapeLayout.xtype,
            cls,
            height: defaultHeight,
            items,
        };
    }

    public doRedMark(...args: any[]) {
        this.text.doRedMark(args);
    }

    public unRedMark(...args: any[]) {
        this.text.unRedMark(args);
    }

    public doClick() {
        super.doClick.apply(this, arguments);
        this.radio.setSelected(this.isSelected());
        if (this.isValid()) {
            this.fireEvent(ParameterRadioGroupSingleSelectRadioItem.EVENT_CHANGE, this.isSelected(), this.getValue(), this);
        }
    }

    public setSelected(v: any) {
        super.setSelected.apply(this, arguments);
        this.radio.setSelected(v);
    }

    public static EVENT_CHANGE = 'EVENT_CHANGE';
}
